.home_section{
    width: 100vw;
    height: 100vh;
}

header{
    width: 100vw;
    height: 15vh;
    background-color: lightblue;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

input{
    padding: 10px 15px;
    border-radius: 30px;
    border: none;
    outline: 0;
    width: 40%;
    font-size: 25px;
    margin: 0 25px;
}

button{
    padding: 15px;
    border-radius: 30px;
    transition: all ease-in-out 0.7s;
}

button:hover{
    background-color: rgb(178, 178, 235);
}

.card_section {
    display: flex;
    flex-wrap:wrap;
}